body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    text-align: center;
    color: #ff4850; 
    background-color: #222222; 
    -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}


h2 {
  font-size: 2em;
  margin: 52px 0 40px;
  text-transform: capitalize;
  color: #051c33;
}

h1 {
  font-family: "Lobster", "Josefin Sans", Helvetica, Arial, sans-serif;
  font-size: 4em;
  color: #051c33;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

a {
  text-decoration: none;
}

.is-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.active {
  background-color: #051c33;
  cursor: pointer;
}

.main-nav li {
  width: 100%;
  margin-bottom: 1px;
}

.main-nav a {
  display: block;
  background: #073a6d;
  border-radius: 3px;
  padding: 5px;
  color: rgb(202, 186, 43);
}

.main-nav a:hover {
  background: #0c58a5;
  color: rgb(238, 219, 53);
}

.main-nav .active {
  background-color: rgb(202, 186, 43);
  color: #073a6d;
}

.main-nav .active:hover {
  background-color: rgb(202, 186, 43);
  color: #073a6d;
}

@media only screen and (max-width: 767px) {
  body {
    
  }

  ul {
    margin-block-start: 2px;
    margin-block-end: 1px;
  }

}

@media only screen and (min-width: 768px) {
  body {
  }

  ul {
    margin-block-start: 1em;
    margin-block-end: 1em;
  }

  .container {
    max-width: 960px;
    margin: auto;
  }

  .main-nav ul {
    display: flex;
    justify-content: center;
  }

  .main-nav li {
    margin: 10px;
    width: 100px;

  }
}
